<template>
  <div style="padding:20px;">
    <Form ref="storeDetail" :model="editData" :rules="rules" label-position="left" :label-width="120">
      <FormItem label="店铺名称：" prop='shopname'>
        <Input v-model='editData.shopname' placeholder='请输入店铺名称' v-if='isEdit'></Input> <span v-else>{{detailData.shopname||'暂无'}}</span>
      </FormItem>
      <FormItem label="公司名称：" prop='company'>
        <Input v-model='editData.company' placeholder='请输入公司名称' v-if='isEdit'></Input> <span v-else>{{detailData.company||'暂无'}}</span>
      </FormItem>
      <FormItem label="商户姓名：" prop='realname'>
        <Input v-model='editData.realname' placeholder='请输入商户姓名' v-if='isEdit'></Input> <span v-else>{{detailData.realname||'暂无'}}</span>
      </FormItem>
      <FormItem label="联系电话：" prop='mobile'>
        <Input v-model='editData.mobile' placeholder='请输入联系电话' v-if='isEdit' :maxlength="11"></Input> <span v-else>{{detailData.mobile||'暂无'}}</span>
      </FormItem>
      <FormItem label="公司邮箱：" prop='mail'>
        <Input v-model='editData.mail' placeholder='请输入邮箱' v-if='isEdit'></Input> <span
        v-else>{{detailData.mail||'暂无'}}</span>
      </FormItem>
      <FormItem label="微信号：" prop='wechat'>
        <Input v-model='editData.wechat' placeholder='请输入微信号' v-if='isEdit'></Input> <span v-else>{{detailData.wechat||'暂无'}}</span>
      </FormItem>
      <FormItem label="店铺地址：" prop='address'>
        <Input v-model='editData.address' placeholder='请输入地址' v-if='isEdit' type='textarea'></Input> <span v-else>{{detailData.address||'暂无'}}</span>
      </FormItem>
      <FormItem label="店铺logo：" prop='logo'>
        <single-uploader :image='editData.logo' v-if='isEdit' @change='imgChange'></single-uploader>
        <span v-else>
        	<img v-if='detailData.logo' :src='detailData.logo' class="logo-show"></img>
        	<span v-else>暂无图片</span>
        </span>
      </FormItem>
      <FormItem label="关于我们：" prop='industry'>
        <vue-editor :content='editData.industry' @change="updateContent" v-if='isEdit'></vue-editor>
        <vue-editor :content='detailData.industry' v-else='isEdit' :disabled='true'></vue-editor>
      </FormItem>
      <FormItem v-if='isEdit'>
        <Button type="primary" @click="handleSave" size="large" style="width:120px" :loading='postEdit'>保存编辑</Button>
        <!-- <Button type="ghost" @click="handleReset" size="large" style="margin-left: 8px;width:80px">取消</Button> -->
      </FormItem>
      <FormItem v-else>
        <Button type="primary" @click="handleEdit" size="large" style="width:120px">编辑/添加</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        detailData: {
          shopname: '',
          logo: '',
          company: '',
          realname: '',
          address: '',
          mobile: '',
          industry: '',
          mail: '',
          wechat: ''
        },
        editData: {
          shopname: '',
          logo: '',
          company: '',
          realname: '',
          address: '',
          mobile: '',
          industry: '',
          mail: '',
          wechat: ''
        },
        rules: {
          shopname: [
            {required: true, message: '店铺名称不能为空！', trigger: 'blur'}
          ],
          logo: [
            {required: true, message: 'logo不能为空！', trigger: 'blur'}
          ],
          company: [
            {required: true, message: '公司名称不能为空！', trigger: 'blur'}
          ],
          mobile: [
            {required: true, message: '联系电话不能为空！', trigger: 'blur'}
          ],
          address: [
            {required: true, message: '地址不能为空！', trigger: 'blur'}
          ],
          industry: [
            {required: true, message: '关于我们不能为空！', trigger: 'blur'}
          ],
        },
        isEdit: false,
        postEdit: false,
      }
    },
    mounted() {
      this.$sitemap.prvname = "系统管理";
      this.$sitemap.currname = '公司信息';
      this.$menuactive.open = ["SystemBlock"];
      this.$menuactive.active = 'Company';
      this.getCompanyDetail()
    },
    methods: {
      imgChange(img) {

        this.editData.logo = img.url;

      },
      handleEdit() {
        this.isEdit = true;
        this.copy(this.detailData, this.editData);
      },
      getCompanyDetail() {
        this.$http.post(this.$api.company_detail, {}).then(res => {
          console.log('商家信息', res.data)
          if (res.data.data && res.data.result == '1') {
            this.copy(res.data.data[0], this.detailData);
          }
        })
      },
      postCompanyEdit(params) {
        return this.$http.post(this.$api.company_edit, Object.assign({}, params, {signescape: ['industry']})).then(res => {
          console.log('编辑商家信息', res.data);
          if (res.data.result == '1') {
            this.$Message.success('信息修改成功！')
          } else {
            this.$Message.error('信息修改失败！')
            return Promise.reject(res.msg)
          }
        }).catch(error => {
          this.$Message.error('信息修改失败！')
          console.log(error)
        })
      },
      handleReset() {
        this.isEdit = false;
      },
      handleSave() {
        this.$refs.storeDetail.validate(valid => {
          if (valid) {
            this.postEdit = true;
            this.postCompanyEdit(this.editData).then(() => {
              this.postEdit = false;
            })
          } else {
            this.$Message.error('信息请填充完整！')
          }
        })

      },
      updateContent(data) {
        this.editData.industry = data
      },
      copy(from, to) {
        console.log('from', from, 'to', to)
        Object.keys(to).forEach(key => {
          to[key] = from[key] ? from[key] : ''
        })
      },
    },

  }

</script>
<style>
  .logo-show {
    display: inline-block;
    width: 150px;
    height: 150px;
    outline: 1px solid silver;
  }

</style>



